.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s; 
    border-radius: 5px;
    background-color: white;
    width: 350px;
    height: auto;
    margin: 1rem;
  padding: 1rem;
    text-align: center;
  }
  
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  }
  
  img {
    border-radius: 5px 5px 0 0;
  }
  
  .container {
    padding: 2px 16px;
  }
  
  .inline{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  align-items: baseline;
  }

  .card h3{
    padding-top: 1rem;
  }

  .placeOrder{
    width: 30%;
  }